import axios from 'axios'
import {Link} from 'react-router-dom'
import React, {useState, useEffect, useContext} from 'react'
import {GlobalState} from '../../../GlobalState'

function Profile() {
    
    const state = useContext(GlobalState)
    const [token] = state.token
    const [posts, setPosts] =  useState([])


    useEffect(() => {
        if (token){
            const getPosts = async() =>{
               const res = await  
               axios.get('/user/infor', {
               headers: {Authorization: token}
              })
           setPosts(res.data)
           console.log(res.data)
            } 
            getPosts()
        }
        
    })

 

    const [user, setUser] = useState({
        password: ""
    })
    const onChangeInput = e =>{
        const {name, value} = e.target;
        setUser({...user, [name]:value})
    }




    const updateSubmit = async e =>{
        
 
        try {
            const res =  await axios.put('/user/update_user',{...user} ,{  
                headers: {Authorization: token}
            });

            console.log(user);

            alert(res.data.msg);

        } catch (err) {
            alert(err.response.data.msg)
        }
    }
    





    
    return (
        <div className="container">
            <br/><br/><br/><br/>
            <div className="row">
                <div className="col-sm"></div>
                <div className="col-sm">
                    <div className="alert alert-primary" role="alert">
                        This Your Profile Info, You can edit Password :
                    </div>
                </div>
                <div className="col-sm"></div>
            </div>
            <div className="row">
                <div className="col-sm"></div>
                <div className="col-sm">
                    <table>                
                    <tr>
                            <td>
                                <label htmlFor="name">
                                    Name :
                                </label>
                            </td>
                            <td>
                                {posts.name}
                           </td>
                        </tr>
                        <br/>
                        <tr>
                            <td>
                                <label htmlFor="email">
                                    Email :
                                </label>
                            </td>
                            <td>
                                {posts.email}
                            </td>
                        </tr>
                        <br/>
                        <tr>
                            <td>
                                <label htmlFor="name">
                                    Created At:
                                </label>
                            </td>
                            <td>
                                {posts.createdAt}
                            </td>
                        </tr>
                        <br/>
                        
                        <tr>
                            <td>
                                <label htmlFor="password">
                                    Password :
                                </label>
                            </td>
                            <td>
                                 <input   onChange={onChangeInput}   className="form-control" type="text" name="password" placeholder= "New Password" />
                            </td>
                        </tr>
                        <br/>
                        <tr>
                            <td>
                                <label htmlFor="cf_password">
                                    Confirm Password : &nbsp;
                                </label>
                            </td>
                            <td>
                                <input  className="form-control" type="text" name="cf_password" id="cf_password" placeholder= "Confirm Your New Password" />
                            </td>
                        </tr>
                        <br/>
                        <tr>
                            <td>          
                                <Link className= "btn btn-danger" to="/">
                                    Back To Home
                                </Link>
                            </td>
                            <td>
                                <button   onClick={updateSubmit} className="form-control btn btn-success">
                                    Update Password
                                </button>
                            </td>
                        </tr>
                    </table> 
                </div>
                <div className="col-sm"></div>
            </div>
        </div>
        
    )
}
export default Profile